import * as React from 'react';
import {useState} from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import {Copyright} from "./components/Copyright";
import dictionary from './lang/dictionary.json'
import {Card, CardContent, Toolbar} from "@mui/material";
import {futuroImp, Group, imperfecto, indefinido, perfecto, Tiempo} from "./lang/grammatica";
import {getRandomInt, shuffleArray} from "./util/util";
import {GroupSettings} from "./components/GroupSettings";
import {ExerciseCard} from "./components/ExerciseCard";
import {ResultCard} from "./components/ResultCard";

const theme = createTheme();

const vocabByGroup = {
  [Group.PERFECTO.key]: findVocabPerfecto(),
  [Group.INDEFINIDO_AR.key]: findVocabIndefinido('ar'),
  [Group.INDEFINIDO_ER_IR.key]: findVocabIndefinido('er').concat(findVocabIndefinido('ir')),
  [Group.IMPERFECTO_AR.key]: findVocabImperfecto('ar'),
  [Group.IMPERFECTO_ER_IR.key]: findVocabImperfecto('er').concat(findVocabImperfecto('ir')),
  [Group.FUTURO_IMP.key]: findVocabFuturoImp(null),
  [Group.FUTURO_IMP_IRR.key]: findVocabFuturoImp('irr')
}

function findVocabPerfecto() {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && desc.perfecto?.group == null
  }).map(el => el[0]);
}

function findVocabIndefinido(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.indefinido?.group == null
  }).map(el => el[0]);
}

function findVocabImperfecto(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.imperfecto?.group == null
  }).map(el => el[0]);
}

function findVocabFuturoImp(group) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && (desc.futuroImp?.group || null) === group
  }).map(el => el[0]);
}


const personas = ['Yo', 'Tu', 'El', 'Nosotros', 'Vosotros', 'Ellos']

function conjugar(tiempo, verbo, persona) {
  if (tiempo === Tiempo.INDEFINIDO) {
    return indefinido(verbo, persona)
  } else if (tiempo === Tiempo.IMPERFECTO) {
    return imperfecto(verbo, persona)
  } else if (tiempo === Tiempo.PERFECTO) {
    return perfecto(verbo, persona)
  } else if (tiempo === Tiempo.FUTURO_IMP) {
    return futuroImp(verbo, persona)
  }
}

function randomCombinations(groups) {
  const combinations = []
  groups.forEach(group => {
    for (let index in personas) {
      combinations.push([group, index])
    }
  })
  shuffleArray(combinations)
  return combinations
}

export default function App() {

  const [state, setState] = useState(() => nextState({
    groups: [Group.FUTURO_IMP], combinations: [], startTimes: [], endTimes: []
  }))
  const [hint, setHint] = useState('')

  function nextState(currState, updatedGroups) {
    const groups = updatedGroups || currState.groups
    let combinations = [...currState.combinations]
    let startTimes = [...currState.startTimes]
    let endTimes = [...currState.endTimes]
    if(endTimes.length < startTimes.length){
      endTimes.push(Date.now())
    }

    if (combinations.length === 0 && currState.result === false) {
      return {
        ...currState,
        groups: groups,
        startTimes: startTimes,
        endTimes: endTimes,
        result: true
      }
    }

    if (combinations.length === 0 || currState.groups !== groups) {
      combinations = randomCombinations(groups)
      startTimes = []
      endTimes = []
    }
    const [group, persona] = combinations.pop()
    const vocab = vocabByGroup[group.key]
    startTimes.push(Date.now())
    return {
      groups: groups,
      combinations: combinations,
      tiempo: group.tiempo,
      verbo: vocab[getRandomInt(vocab.length)],
      persona: persona,
      startTimes: startTimes,
      endTimes: endTimes,
      result: false
    }
  }

  function findHintFor(state) {
    return conjugar(state.tiempo, state.verbo, state.persona)
  }

  function onClickNext() {
    setHint('')
    setState(nextState)
  }

  function onClickHint() {
    setHint(findHintFor(state))
    setState(currState => {
      const endTimes = [...currState.endTimes]
      endTimes.push(Date.now())
      return {
        ...currState,
        endTimes: endTimes
      }
    })
  }

  function getProgress() {
    const total = state.groups.length * personas.length
    return total - state.combinations.length + '/' + total
  }

  function getExercise() {
    if (hint) {
      return hint
    } else {
      return state.verbo
    }
  }

  function getAverageTime(){
    const times = state.startTimes.map((startTime, idx) => {
      return state.endTimes[idx] - startTime
    })
    const sum = times.reduce((a, b) => a + b, 0);
    const result = (sum / times.length) || 0
    return Math.round(result);
  }

  function onGroupSettingsClose(groups) {
    setState(currState => nextState(currState, groups))
    setHint('')
  }

  function getCard() {
    if (state.result === true) {
      return <ResultCard averageTime={getAverageTime()}/>
    } else {
      return <ExerciseCard exercise={getExercise()} hint={hint} persona={personas[state.persona]}
                           progress={getProgress()} tiempo={state.tiempo}/>;
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline/>
        <Toolbar disableGutters sx={{borderBottom: 1, borderColor: 'divider'}}>
          <Typography
            variant="h5"
            color="inherit"
            align="left"
            sx={{flex: 1}}
          >
            Conjugador
          </Typography>
          <GroupSettings initGroups={state.groups} onClose={(groups) => onGroupSettingsClose(groups)}/>
        </Toolbar>
        {getCard()}
        <Grid container spacing={2} sx={{mt: 1}}>
          <Grid item xs={5}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickHint}>Pista</Button>
          </Grid>
          <Grid item xs={7}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickNext}>Seguir</Button>
          </Grid>
        </Grid>
        <Copyright sx={{mt: 4, mb: 4}}/>
      </Container>
    </ThemeProvider>
  );
}